import axios from 'axios'
import { ElMessage, ElMessageBox } from 'element-plus'
import Cookies from 'js-cookie'

// 1. ÂàõÂª∫AxiosÂÆû‰æã
// ÂΩªÂ∫ïÂÖºÂÆπ Vue CLIÔºàwebpackÔºâÔºåÊ∂àÈô§ process is not defined Êä•Èîô
let baseURL = '/api'

// Ê†∏ÂøÉ‰øÆÂ§çÔºöÊâÄÊúâ process ËÆøÈóÆÈÉΩÂåÖË£πÂú® try/catch + window ÂÖúÂ∫ï
try {
  // ‰ºòÂÖàËØªÂèñ Vue CLI Ê≥®ÂÖ•ÁöÑ process.envÔºàÈÄöËøá window ËÆøÈóÆÔºåÈÅøÂÖçË£∏ÂºïÁî®Ôºâ
  const env = window.process?.env || {};
  // ËØªÂèñ VUE_APP_API_BASE_URLÔºà‰Ω†ÁöÑÁéØÂ¢ÉÂèòÈáèÔºâ
  if (env.VUE_APP_API_BASE_URL) {
    baseURL = env.VUE_APP_API_BASE_URL;
  }
  // Êú¨Âú∞ÂºÄÂèëÁéØÂ¢ÉÂÖúÂ∫ïÔºàÈÅøÂÖçÊú™ÈÖçÁΩÆÊó∂ËØ∑Ê±Ç404Ôºâ
  else if (env.NODE_ENV === 'development' && baseURL === '/api') {
    baseURL = 'http://127.0.0.1:8000/api';
    console.warn('[request] Êú¨Âú∞ÂºÄÂèëÊ®°ÂºèÔºåÈªòËÆ§APIÂú∞ÂùÄÔºö', baseURL);
  }
} catch (e) {
  // ÊµèËßàÂô®ÁéØÂ¢ÉÊó† process Êó∂ÔºåÁõ¥Êé•Áî®‰ª£ÁêÜÂú∞ÂùÄ /apiÔºà‰æùËµñ vue.config.js ‰ª£ÁêÜÔºâ
  baseURL = '/api';
  console.warn('[request] ÁéØÂ¢ÉÂèòÈáèËØªÂèñÂ§±Ë¥•Ôºå‰ΩøÁî®‰ª£ÁêÜÂú∞ÂùÄÔºö', baseURL);
}

const request = axios.create({
  baseURL, // ‰ªéÁéØÂ¢ÉÂèòÈáè/ÂÖúÂ∫ïËé∑ÂèñÂü∫Á°ÄURL
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json;charset=utf-8'
  }
})

// 2. ËØ∑Ê±ÇÊã¶Êà™Âô®ÔºöÊ∑ªÂä†‰ª§Áâå„ÄÅÂ§ÑÁêÜËØ∑Ê±ÇÂâçÈÄªËæë
request.interceptors.request.use(
  (config) => {
    // ‰ªé cookie Êàñ localStorage Ëé∑Âèñ‰ª§ÁâåÔºàÁôªÂΩïÂêéÂ≠òÂÇ®Ôºâ
    // ‰∏Ä‰∫õÊóß‰ª£Á†ÅÂ∞Ü token Â≠òÂà∞ localStorageÔºåÊâÄ‰ª•ËøôÈáåÂêåÊó∂ÂÖºÂÆπ‰∏§ÁßçÊÉÖÂÜµ
    const token = Cookies.get('session_token') || window.localStorage.getItem('session_token')
    if (token) {
      // Ê∑ªÂä†‰ª§ÁâåÂà∞ËØ∑Ê±ÇÂ§¥ÔºàÂêéÁ´ØÈÄöËøáAuthorizationËé∑ÂèñÔºâ
      config.headers.Authorization = `Bearer ${token}`
    }
    // Â¶ÇÊûúËØ∑Ê±Ç‰ΩìÊòØ FormDataÔºåÂà†Èô§ÈªòËÆ§ Content-TypeÔºåËÆ©ÊµèËßàÂô®/axios Ëá™Âä®ËÆæÁΩÆ boundary
    if (config.data && typeof FormData !== 'undefined' && config.data instanceof FormData) {
      try {
        delete config.headers['Content-Type']
      } catch (e) {}
    }
    return config
  },
  (error) => {
    // ËØ∑Ê±ÇÈîôËØØÔºàÂ¶ÇÁΩëÁªú‰∏≠Êñ≠Ôºâ
    ElMessage.error('ËØ∑Ê±ÇÂèëÈÄÅÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªú')
    return Promise.reject(error)
  }
)

// 3. ÂìçÂ∫îÊã¶Êà™Âô®ÔºöÁªü‰∏ÄÂ§ÑÁêÜÂìçÂ∫î„ÄÅÈîôËØØÁ†Å
request.interceptors.response.use(
  (response) => {
    const res = response.data  // ÂêéÁ´ØËøîÂõûÁöÑJSONÊï∞ÊçÆ

    // 3.1 Â§ÑÁêÜÊàêÂäüÂìçÂ∫îÔºàÊ†πÊçÆÂêéÁ´ØÁ∫¶ÂÆöÁöÑcodeÂà§Êñ≠Ôºâ
    if (res.code === 200) {
      return res  // Áõ¥Êé•ËøîÂõûÊï∞ÊçÆÔºåÁªÑ‰ª∂‰∏≠ÂèØÁõ¥Êé•‰ΩøÁî®res.data
    }

    // 3.2 Â§ÑÁêÜ‰∏öÂä°ÈîôËØØÔºàÂêéÁ´ØËøîÂõûÈùû200Áä∂ÊÄÅÁ†ÅÔºâ
    ElMessage.error(res.msg || 'Êìç‰ΩúÂ§±Ë¥•')
    return Promise.reject(new Error(res.msg || 'Error'))
  },
  (error) => {
    // 4. Â§ÑÁêÜHTTPÈîôËØØÔºàÂ¶Ç401„ÄÅ500Á≠âÔºâ
    const status = error.response?.status

    switch (status) {
      case 401:
        Cookies.remove('session_token')
        ElMessageBox.alert('ÁôªÂΩïÁä∂ÊÄÅÂ∑≤Â§±ÊïàÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï', 'ÊèêÁ§∫', {
            confirmButtonText: 'Á°ÆÂÆö',
            // Êîπ‰∏∫asyncÂõûË∞ÉÔºåÂä®ÊÄÅÂØºÂÖ•router
            callback: async () => {
            // Âä®ÊÄÅÂØºÂÖ•Ë∑ØÁî±Ê®°ÂùóÔºåÊâìÁ†¥Âæ™ÁéØ‰æùËµñ
            const { default: router } = await import('@/router')
            router.push('/login')  // Âä®ÊÄÅËé∑ÂèñrouterÂêéÂÜç‰ΩøÁî®
            }
        })
        break
      case 403:
        ElMessage.error('Ê≤°ÊúâÊùÉÈôêÊâßË°åÊ≠§Êìç‰Ωú')
        break
      case 404:
        ElMessage.error('ËØ∑Ê±ÇÁöÑÊé•Âè£‰∏çÂ≠òÂú®')
        break
      case 500:
        ElMessage.error('ÊúçÂä°Âô®ÂÜÖÈÉ®ÈîôËØØÔºåËØ∑Á®çÂêéÈáçËØï')
        break
      default:
        ElMessage.error(`ËØ∑Ê±ÇÂ§±Ë¥•Ôºà${status || 'Êú™Áü•ÈîôËØØ'}Ôºâ`)
    }

    return Promise.reject(error)
  }
)

// 4. Â∞ÅË£ÖÂ∏∏Áî®ËØ∑Ê±ÇÊñπÊ≥ïÔºàÂèØÈÄâÔºåÁÆÄÂåñÁªÑ‰ª∂Ë∞ÉÁî®Ôºâ
export const requestMethod = {
  // GETËØ∑Ê±Ç
  get: (url, params = {}) => {
    return request({
      url,
      method: 'get',
      params
    })
  },

  // POSTËØ∑Ê±ÇÔºàJSONÊ†ºÂºèÔºâ
  post: (url, data = {}, params = {}) => {
    return request({
      url,
      method: 'post',
      data,
      params
    })
  },

  // PUTËØ∑Ê±Ç
  put: (url, data = {}, params = {}) => {
    return request({
      url,
      method: 'put',
      data,
      params
    })
  },

  // DELETEËØ∑Ê±Ç
  delete: (url, params = {}) => {
    return request({
      url,
      method: 'delete',
      params
    })
  },

  // Ë°®ÂçïÊèê‰∫§ÔºàÂ¶ÇÊñá‰ª∂‰∏ä‰º†Ôºâ
  postForm: (url, data = {}, params = {}) => {
    return request({
      url,
      method: 'post',
      data,
      params,
      // ‰∏çÊâãÂä®ËÆæÁΩÆ Content-TypeÔºåËÆ©ÊµèËßàÂô®/axios ‰∏∫ FormData Ëá™Âä®Ê∑ªÂä† boundary
    })
  }
}

export default request